import * as React from 'react';
import Post from './Post';
import axios from 'axios';
import { Component } from 'react';

type FeedState = { data: [] }
export default class Feeds extends Component<{}, FeedState> {
    constructor(props: any){
        super(props)
        
        this.state = {
          data: []
        }
      }
    
      getData(){
        axios.get('http://jsonplaceholder.typicode.com/users').then(res => {
          var data = res.data
            this.setState({data : data})
        })
      }
      componentDidMount(){
        this.getData()
      }
    render() {
        return (
            <>
                <ul>
                    {this.state.data.map((d: any) => (<Post>{d.name}</Post> ))} 
                </ul>
            </>
        )
    }
}
// export default Feed;
